@media (max-width:1050px) {
	section.hi {
		.hi-left {
			top: -35px;
			min-height: 425px;
		}
	}
}

@media (max-width: 980px) {
	section.what {

		h2 {
			margin: 0 10% 0 0;
			&::before {
				width: 40%;
				left: -20px;
			}

		}

		.what-container {
			&:nth-of-type(odd) h4{
				left: 110%;
			}
		}

		h4 {
			left: -40%;
		}
	}
}


@media (max-width:930px) {
	section.hi {
		.hi-left {
			top: -100px;
			bottom: 100px;
			min-height: 450px;
		}
	}

	footer {

		.footer-container {
			
			width: 80%;
		}
	}
}

@media (max-width:768px) {
	section.splash {
		h1 {
			font-size: 2.75rem;
		}

		h3 {
			font-size: 1rem;
		}
	}

	section.recent {
		.recent-container {
			border: none;
		}

		figure {
			width: 100%;
			margin: 150px auto;
			display: block;

			&:nth-of-type(2) {
				margin: 150px auto;
			}
			&::after {
				display: block;
				content: '';
				background: $outline-grey;
				width: 100%;
				height: 1px;
				position: relative;
				top: 75px;
				left: 50%;
				transform: translateX(-50%);
			}

			&:first-of-type {
				margin-top: 10px;
			}

			&:nth-child(3):after {
				display: none;
			}
		}
	}

	section.hi {
		padding-top: 8%;
		padding-bottom: 0;
	}

	section.what {
	
		h2::before {
    		width: 35%;
    		left: -40px;
    	}

		.what-container {
			margin: 15% auto;
			padding: 0;
			&:nth-of-type(odd) h4{
				left: 120%;
			}
		}

		h4 {
			left: -65%;
			top: 25%;
		}
	}

	//portfolio template responsive
	header.portfolio-header {
		position: relative;
		i.hamburger-portfolio {
			padding-top: 0;
			width: 10%;
			position: absolute;
			top: 30%;
			right: 0;
			bottom: 0;
			left: 0;
			z-index: 20;
		}
		.portfolio-nav {
			position: absolute;
			left: -100%;
			z-index: 15;
			transition: all 0.3s ease;
			white-space: nowrap;
			.menu {
				display: flex;
				flex-direction: column;
				position: relative;
				top: 100px;
				left: -42%;
				li {
					flex-basis: 10%;
				}

				a {
					color: $text-grey;

				}
			}
		}

		.portfolio-nav-open {
			left: 10%;
			z-index: 15;
			transition: all 0.3s ease;
			white-space: nowrap;
		}
	

		h1.portfolio-header-title {
			flex-basis: 100%;
			position: relative;
			z-index: 10;
		}
		
		i.fa {
			display: block;
		}

		.portfolio-social {
			display: none;
		}
	}
}

@media (max-width:700px) {
	section.hi {
		.hi-left {
			top: -100px;
			bottom: 100px;
			min-height: 500px;
		}
	}

	footer {
		.copyright {
			.copyright-left {
				float: none;
				text-align: center;
			}

			.copyright-right {
				float: none;
				text-align: center;
			}
		}
	}
}

@media (max-width:600px) {
	header {
		i.hamburger-main {
			display: block;
			text-align: center;
			padding: 0;
			padding-top: 3%;
			&:hover {
				opacity: 1;
			}
		}
		
		ul.menu li {
			margin-right: 0;
		}

		.nav {
			margin-left: 0;
		}

		.nav-open {
			left: 0;
			display: flex;
			justify-content: center;
			transition: all 0.3s ease;
		}
	}

	section.splash {
		h1 {
			padding: 0 4%;
		}

		h3 {
			font-size: 1rem;
		}
	}

	section.what {
		padding-top: 0;

		h2 {
			margin: 0;
			text-align: center;
			&::before {
				display: none;
			}
		}

		.what-container {
			width: 80%;
			margin: 25% auto;

			&:nth-of-type(odd) h4{
				left: 100%;
			}
		}

		.container {
			div:nth-child(2) {
				margin-top: 10%;
			}
		}
		
		h3 {
			text-transform: none;
			font-size: 1.7rem;
		}

		h4 {
			position: static;
			text-align: center;
			margin:0;
			margin-bottom: 13%;
		}

		hr {
			width: 40%;
			margin: 0 auto;
			position: relative;
			top: -10%;
			bottom: 75px;
		}

		hr:first-of-type {
			position: static;
			width: 80%;
			transform: rotate(-25deg);
		}

		hr:nth-of-type(2) {
			position: static;
			width: 80%;
			transform: rotate(25deg);
		}

		hr:last-of-type {
			display: none;
		}
	}

	section.hi {
		.container {
			width: 80%;
		}

		.hi-left, .hi-right {
			display: block;
			width: 100%;
		}
		.hi-left {
			background: transparent;
			min-height: 450px;
			margin: 8% 0;
			position: relative;
			left: 0;
			h3 {
				text-transform: none;
			}
		}

		.hi-right {
			position: relative;
			left: 0;
			top: -125px;
			width: 80%;
			margin: 0 auto;
		}

		.hi-outline {
			padding: 10%;
			background: transparent;
			border: 1px solid $outline-grey;
			position: absolute;
			left: 0;
			right: 0;
			top: 10%;
			bottom: -10%;

		}
	}

	footer {
		#menu-social {
			padding: 3% 0 20%;
			margin: 0 auto;
			width: 50%;
			display: flex;
			flex-direction: column;
			justify-content: space-between;
			align-items: center;
			li {
				flex: 1 0 auto;
			}
		}

		.copyright {
			p {
				margin: 0;
			}
		}
	}

	//portfolio template responsive 


	.portfolio {
		img.description-image {
			width: 100%;
			&:first-of-type {
				margin-right: 0;
			}
			&:last-of-type {
				margin-left: 0;
			}
		}
	}
}

@media (max-width: 468px) {
	section.splash {
		h1 {
			font-size: 2rem;
		}
	}

	section.recent {
		h2 {
			font-size: 1.5rem;
		}

		figure {
			margin: 100px auto;

			&:nth-of-type(2) {
				margin: 100px auto;
			}
			&::after {
				top: 50px;

				&:last-of-type {
					display: none;
				}
			}
		}
	}

	.portfolio {
		.description-container {
			width: 80%;
		}
	}
}

